<template>
  <div>
    <router-link
      tag="h4"
      class="title clearfix"
      :to="`topic/${item.id}`"
      v-for="item in arr"
      :key="item.id"
    >
      <h4>{{ item.title }}</h4>
      <span class="time">{{ item.last_reply_at | moment("from", "now") }}</span>
    </router-link>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="
        $route.fullPath === '/'
          ? 1000
          : $route.fullPath === '/?tab=good'
          ? 10
          : $route.fullPath === '/?tab=weex'
          ? 10
          : $route.fullPath === '/?tab=job'
          ? 20
          : 500
      "
      @current-change="change"
      :current-page="num"
    >
    </el-pagination>
  </div>
</template>
<script>
export default {
  name: "list",
  data() {
    return {
      arr: [],
      num: 1
    };
  },
  computed: {
    titlelength() {
      return this.arr.length;
    }
  },
  created() {
    const { fullPath } = this.$route;
    this.get(fullPath, 1);
  },
  watch: {
    "$route.fullPath"(newValue) {
      window.console.log(this.num);
      this.num = 1;
      this.axios
        .get(`https://www.vue-js.com/api/v1/topics/${newValue}`)
        .then(res => {
          this.arr = res.data.data;
        });
    }
  },

  methods: {
    get(fullPath, ad) {
      this.axios
        .get(
          `https://www.vue-js.com/api/v1/topics/${
            fullPath === "/" ? "/?" : fullPath
          }&page=${ad}`
        )
        .then(res => {
          this.arr = res.data.data;
          window.console.log(this.arr.length);
        });
    },
    change(a) {
      const { fullPath } = this.$route;
      this.num = a;
      this.get(fullPath, a);
    }
  }
};
</script>

<style>
.title h4 {
  float: left;
  width: 70%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.title {
  border-bottom: 1px solid rgb(245, 242, 242);
  padding: 10px 0;
  padding-left: 6%;
  background-color: rgb(250, 251, 253);
}
</style>
