<template>
  <header>
    <div class="h-in clearfix">
      <div class="left">
        <div @click="logo" class="logo">
          <img src="https://www.vue-js.com/public/images/vue.png" alt="" />
        </div>

        <span class="vue">vue.js</span>
        <div v-if="!login1" class="box">
          <input
            v-model="token"
            id="sign"
            type="text"
            placeholder="请输入账号"
            :style="{ backgroundColor: shi ? '#fff' : 'rgb(6, 201, 39)' }"
            @mouseenter="shi = true"
            @mouseleave="shi = false"
          /><button class="in" for="sign" @click="login">登录</button>
        </div>
        <div class="noyou" v-else>
          <button @click="tui" class="tui">推出</button>
        </div>
      </div>
      <ul class="clearfix">
        <li @click="logo">首页</li>
        <router-link to="/title" tag="li">发布话题</router-link>
        <li>未读消息</li>
        <li>微信公众号</li>
        <li>vue 2.0</li>
        <li>参考资料</li>
        <li>api</li>

        <li>设置</li>
      </ul>
    </div>
  </header>
</template>

<script>
import axios from "axios";
export default {
  name: "header1",
  data() {
    return {
      shi: false,
      token: "",
      login1: false
    };
  },
  created() {
    if (sessionStorage.getItem("token")) {
      axios
        .post("https://www.vue-js.com/api/v1/accesstoken", {
          accesstoken: sessionStorage.getItem("token")
        })
        .then(() => {
          this.login1 = true;
        });
    }
  },
  methods: {
    login() {
      axios
        .post("https://www.vue-js.com/api/v1/accesstoken", {
          accesstoken: this.token
        })
        .then(() => {
          sessionStorage.setItem("token", this.token);
          this.login1 = true;
        });
      this.$router.push("/");
    },
    tui() {
      this.login1 = false;
      this.$router.push("/");
    },
    logo() {
      this.$router.push("/");
    }
  }
};
</script>

<style>
header {
  width: 100%;
  background-color: rgb(4, 100, 4);
}
.h-in {
  width: 86%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.h-in div img {
  width: 40px;
}
.left {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
ul {
  margin-right: 1px;
}
.h-in > ul > li {
  list-style: none;
  float: left;
  color: #fff;
  cursor: pointer;
}
.h-in > ul > li + li {
  margin-left: 20px;
}
.vue {
  font-size: 20px;
  color: #fff;
  font-weight: 500;
  margin-left: 20px;
  margin-right: 20px;
}
.in,
.tui {
  border: none;
  outline: none;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  color: rgb(255, 255, 255);
  font-size: 16px;
  text-decoration: underline;
}
#sign {
  margin-right: 10px;
  border-radius: 10px;
  padding-left: 10px;
  height: 20px;
  line-height: 20px;
  background-color: rgb(6, 201, 39);
  border: 0;
  outline: none;
}
#sign::-webkit-input-placeholder {
  font-size: 12px;
}
@media screen and (max-width: 414px) {
  header {
    height: 10%;
  }
  li {
    float: left;
  }
  ul {
    width: 100%;
  }
  .h-in {
    margin: none;
  }
}
</style>
