<template>
  <div>
    <div class="iu">
      <span class="asd"
        >{{ title }} <button @click="quxiao" class="quxiao">取消收藏</button>
        <button v-show="dianji" @click="shou" class="shoucang">加入收藏</button>
      </span>
      <div class="op" v-html="arr.content"></div>
      <h5 class="sa"></h5>

      <div class="huifu">
        <h5>添加回复</h5>
        <textarea
          style="resize:none"
          class="ping"
          cols="30"
          rows="10"
          v-model="content"
        ></textarea>
        <button @click="addmo" class="hui">回复</button>
        <ul>
          <li v-for="item in arrhui" :key="item.id">
            {{ item.content }} <span @click="zan" class="zan">👍</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "small1",
  data() {
    return {
      arr: [],
      content: "",
      arrhui: [],
      dianji: true
    };
  },
  props: ["title"],

  mounted() {
    const path = this.$route.params.id;
    window.console.log(this.$route);
    this.add(path);
  },
  methods: {
    add(path) {
      this.axios
        .get(`https://www.vue-js.com/api/v1/topic/${path}`)
        .then(res => {
          this.arr = res.data.data;
        });
    },
    addmo() {
      const path = this.$route.params.id;
      this.arrhui = [{ content: this.content }, ...this.arrhui];
      this.axios
        .post(`https://www.vue-js.com/api/v1/topic/${path}/replies`, {
          accesstoken: sessionStorage.getItem("token"),
          content: this.content
        })
        .then(res => {
          window.console.log(res);
        });
      this.content = "";
    },
    zan() {
      const path = this.$route.params.id;
      window.console.log(path);
      this.axios
        .post(`https://www.vue-js.com/api/v1/reply/${path}/ups`, {
          accesstoken: sessionStorage.getItem("token")
        })
        .then(res => {
          window.console.log(res);
        });
    },
    shou() {
      const path = this.$route.params.id;
      this.axios
        .post(`https://www.vue-js.com/api/v1/topic/collect `, {
          accesstoken: sessionStorage.getItem("token"),
          topic_id: path
        })
        .then(res => {
          if (res.data.success) {
            this.dianji = false;
          }
          window.console.log(res.data.success);
        });
    },
    quxiao() {
      const path = this.$route.params.id;
      this.axios
        .post(`https://www.vue-js.com/api/v1/topic/de_collect `, {
          accesstoken: sessionStorage.getItem("token"),
          topic_id: path
        })
        .then(res => {
          if (res.data.success) {
            this.dianji = true;
          }
          window.console.log(res.data.success);
        });
    }
  }
};
</script>

<style>
p {
  font-size: 15px;
  line-height: 2.2em;
  overflow: auto;
}
.op {
  padding-top: 10px;
  padding-left: 6%;
  padding-right: 6%;
}
img {
  width: 60%;
}
.ping {
  width: 100%;
  height: 200px;
  margin-bottom: 1%;
}
.huifu {
  width: 90%;
  margin: 0 auto;
  padding-top: 20px;
}
.iu > h5 {
  background-color: #ccc;
  line-height: 30px;
}
.sa {
  height: 20px;
  background-color: #ccc;
}
.huifu > h5 {
  margin-bottom: 1%;
}
.hui {
  border: 0;
  background-color: rgb(80, 105, 247);
  padding: 5px 10px 5px 10px;
  color: #fff;
}
.huifu > ul {
  width: 100%;
  margin-top: 2%;
}
li {
  list-style: none;
}
.huifu > ul > li {
  border-bottom: 1px solid #ccc;
  height: 40px;
  line-height: 40px;
}
.zan {
  float: right;
  cursor: pointer;
}
code {
  display: block;
  width: 200px;
  overflow: hidden;
  height: auto;
  word-wrap: break-word;
  word-break: break-all;
}
.iu {
  position: relative;
}
.quxiao,
.shoucang {
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  outline: none;
  position: absolute;
  top: 10px;
  right: 5%;
}
.shoucang {
  background-color: rgb(8, 155, 8);
  color: #fff;
}
.quxiao {
  background-color: rgb(199, 201, 199);
  color: rgb(58, 58, 58);
}
</style>
