<template>
  <div class="title1">
    <div class="kong"></div>
    <div class="ti-in">
      <h3 class="biaoti">发布话题</h3>
      <div class="bankuai">
        <span>请选择板块：</span>
        <input
          type="radio"
          id="ask"
          v-model="picked1"
          value="ask"
        />
        <label for="ask">ask</label>
        <input
          type="radio"
          id="job"
          v-model="picked1"
          value="job"
        />
        <label for="job">job</label>
        <input
          type="radio"
          id="share"
          v-model="picked1"
          value="share"
        />
        <label for="share">share</label>
      </div>
      <input
        class="shuru"
        placeholder="请输入标题"
        type="text"
        v-model="shuru"
      >
      <textarea
        placeholder="请输入内容"
        style="resize:none"
        class="neirong"
        id=""
        cols="30"
        rows="10"
        v-model="neirong"
      ></textarea>
      <button
        @click="add"
        class="tijiao"
      >提交</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "title1",
  data() {
    return {
      picked1: "",
      shuru: "",
      neirong: ""
    };
  },
  methods: {
    add() {
      window.console.log(this.picked1);
      window.console.log(this.shuru);
      window.console.log(this.neirong);
      this.axios
        .post("https://www.vue-js.com/api/v1/topics", {
          title: this.shuru,
          tab: this.picked1,
          content: this.neirong,
          accesstoken: sessionStorage.getItem("token")
        })
        .then(res => {
          window.console.log(res);
        });
    }
  }
};
</script>

<style>
.title1 {
  width: 100%;
  height: 100%;
}
.ti-in {
  width: 90%;
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
}
.kong {
  width: 100%;
  height: 20px;
  background-color: rgb(201, 199, 199);
}
.biaoti {
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #ccc;
  color: rgb(160, 158, 158);
}
.bankuai {
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #ccc;
}
.bankuai > label {
  margin-right: 2%;
  color: rgb(73, 73, 73);
}
.bankuai > span {
  color: rgb(73, 73, 73);
}
.shuru {
  width: 100%;
  height: 30px;
  margin-top: 20px;
  padding-left: 10px;
  border-radius: 10px;
  background-color: rgb(247, 244, 244);
  border: 1px solid #ccc;
  outline: none;
}
.neirong {
  width: 100%;
  margin-top: 20px;
  height: 200px;
  padding: 10px;
  border-radius: 10px;
  outline: none;
}
.tijiao {
  margin-top: 10px;
  width: 70px;
  height: 30px;
  line-height: 30px;
  background-color: rgb(69, 86, 240);
  border: 0;
  border-radius: 10px;
  color: #fff;
  outline-color: #fff;
}
</style>